<template>
  <div class="classification">
    <head-top title="分类" />
    <div class="class-content">
      <div v-for="(item, i) in course" :key="i" @click="ToPage(item.value)">
        {{ item.label }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      course: []
    };
  },
  mounted() {
    this.GetCatory();
  },
  methods: {
    ToPage(id) {
      this.$router.push({
        path: "/source/classification-detail",
        query: { id: id }
      });
    },
    // 获取条目
    async GetCatory() {
      const res = await this.$mainHttp.Metadata.GetTrees({ code: "JPKCLM" });
      if (res.Datas) {
        res.Datas.forEach(ele => {
          if (ele.MetadataCode !== "JPKCLM") {
            this.course.push({
              value: ele.MetadataCode,
              label: ele.MetadataName
            });
          }
        });
      }
    }
  }
};
</script>

<style lang="less">
.classification {
  background-color: #fff;
  #headTop .header_title {
    font-size: 18px;
  }
  .class-content {
    height: calc(100vh - 45px);
    overflow: auto;
    background-color: #fff;
    text-align: left;
  }
  .class-content > div {
    display: inline-block;
    width: calc(33vw - 15px);
    height: 30px;
    background: rgba(255, 255, 255, 1);
    border: 1px solid rgba(221, 221, 221, 1);
    border-radius: 3px;
    font-size: 14px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    text-align: center;
    line-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 25px;
    margin-left: 10px;
  }
  .class-content > .active {
    color: rgb(0, 203, 171);
    border: 1px solid rgb(0, 203, 171);
  }
}
</style>



